import React, { forwardRef } from "react";
import { colorPalette } from "../../constants/colorPalette";
import {
  ColorBox,
  ColorDescription,
  ColorName,
  ColorPaletteList,
  Container,
  Header,
  Info,
  InfoContainer,
  Link,
  Notice,
  Title,
} from "./styles";

const PaletteContainer = forwardRef(({ product }, ref) => {
  return (
    <Container ref={ref} product={product}>
      <Header product={product}>
        {product ? null : <Title> </Title>}

        <InfoContainer>
          <Notice>  !</Notice>
          <Info>
                    
             ,      
                   .
          </Info>

          <Info>
              ,        
            .
          </Info>

          <Info>
                    
                 .
          </Info>

          <Info>
                  {" "}
            <Link>Spectrum Express</Link>
          </Info>
        </InfoContainer>
      </Header>

      <ColorPaletteList>
        {colorPalette.map(
          ({ background, colorName, colorDescription }, index) => (
            <ColorBox
              key={`${background}-${index}`}
              backgroundcolor={background}
            >
              <ColorName>{colorName}</ColorName>
              <ColorDescription>{colorDescription}</ColorDescription>
            </ColorBox>
          )
        )}
      </ColorPaletteList>
    </Container>
  );
});

export default PaletteContainer;
